.fullpage {
    width: 100vw;
    height: 1000px;
}

.ContentAd_container {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ContentAd_textinfo {
    position: relative;
    color: white;
    bottom: 0;
}

.ContentAd {
    width:  calc(100vw - 170px);
    /* margin: 80px 0px; */
    height: calc(100vh - 160px);
    /* border: black 2px solid; */
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.testingImg {
    width: 100%;
    height: auto;
    margin-bottom: 285px;
    z-index: -1;
}